"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { deleteNews, getNews } from "@/services/news.service";
import AdminLayout from '@/components/admin/AdminLayout';

export default function AdminNews() {
  const router = useRouter();
  const [news, setNews] = useState<any[]>([]);
  const [filtered, setFiltered] = useState<any[]>([]);
  const [loading, setLoading] = useState(true);

  const [statusFilter, setStatusFilter] = useState("all");
  const [typeFilter, setTypeFilter] = useState("all");
  const [search, setSearch] = useState("");

  const fetchNews = async () => {
    setLoading(true);
    try {
      const res = await getNews();
      setNews(res.data.data); // pagination
      setFiltered(res.data.data);
    } catch (e) {
      console.error("Fetch news error", e);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchNews();
  }, []);

  useEffect(() => {
    let temp = [...news];

    if (statusFilter !== "all") {
      temp = temp.filter((n) => n.status === statusFilter);
    }

    if (typeFilter !== "all") {
      temp = temp.filter((n) => n.type === typeFilter);
    }

    if (search.trim() !== "") {
      const lower = search.toLowerCase();
      temp = temp.filter(
        (n) =>
          n.title.toLowerCase().includes(lower) ||
          n.summary?.toLowerCase().includes(lower)
      );
    }

    setFiltered(temp);
  }, [statusFilter, typeFilter, search, news]);

  const handleDelete = async (uuid: string) => {
    if (!confirm("ŸáŸÑ ÿ£ŸÜÿ™ ŸÖÿ™ÿ£ŸÉÿØ ŸÖŸÜ ÿ≠ÿ∞ŸÅ ÿßŸÑÿÆÿ®ÿ±ÿü")) return;
    await deleteNews(uuid);
    fetchNews();
  };

  if (loading) {
    return (
      <p className="text-center text-gray-400 animate-pulse">
        ÿ¨ÿßÿ±Ÿä ÿ™ÿ≠ŸÖŸäŸÑ ÿßŸÑÿ£ÿÆÿ®ÿßÿ±...
      </p>
    );
  }

  return (
      <AdminLayout>
    <div>
      <h1 className="text-3xl font-bold mb-6">ÿ•ÿØÿßÿ±ÿ© ÿßŸÑÿ£ÿÆÿ®ÿßÿ±</h1>

      {/* Toolbar */}
      <div className="flex flex-wrap gap-3 mb-6 items-center">
        <button
          onClick={() => router.push("/admin/news/create")}
          className="bg-green-600 px-4 py-2 rounded hover:bg-green-700"
        >
          ‚ûï ÿ•ÿ∂ÿßŸÅÿ© ÿÆÿ®ÿ±
        </button>

        <select
          onChange={(e) => setStatusFilter(e.target.value)}
          className="bg-gray-800 p-2 rounded"
        >
          <option value="all">ŸÉŸÑ ÿßŸÑÿ≠ÿßŸÑÿßÿ™</option>
          <option value="published">ŸÖŸÜÿ¥Ÿàÿ±</option>
          <option value="draft">ŸÖÿ≥ŸàÿØÿ©</option>
          <option value="archived">ÿ£ÿ±ÿ¥ŸäŸÅ</option>
        </select>

        <select
          onChange={(e) => setTypeFilter(e.target.value)}
          className="bg-gray-800 p-2 rounded"
        >
          <option value="all">ŸÉŸÑ ÿßŸÑÿ£ŸÜŸàÿßÿπ</option>
          <option value="article">ŸÖŸÇÿßŸÑ</option>
          <option value="video">ŸÅŸäÿØŸäŸà</option>
        </select>

        <input
          type="text"
          placeholder="ÿ®ÿ≠ÿ´..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="bg-gray-800 p-2 rounded"
        />
      </div>

      {/* Table */}
      <div className="overflow-x-auto">
        <table className="w-full bg-gray-900 rounded">
          <thead className="bg-gray-800 text-right">
            <tr>
              <th className="p-3">#</th>
              <th className="p-3">ÿßŸÑÿπŸÜŸàÿßŸÜ</th>
              <th className="p-3">ÿßŸÑŸÜŸàÿπ</th>
              <th className="p-3">ÿßŸÑÿ≠ÿßŸÑÿ©</th>
              <th className="p-3">ÿπÿßÿ¨ŸÑ</th>
              <th className="p-3">ÿßŸÑÿ™ÿßÿ±ŸäÿÆ</th>
              <th className="p-3">ÿ•ÿ¨ÿ±ÿßÿ°ÿßÿ™</th>
            </tr>
          </thead>
          <tbody>
            {filtered.map((item, index) => (
              <tr
                key={item.uuid}
                className="border-b border-gray-800 hover:bg-gray-800"
              >
                <td className="p-3">{index + 1}</td>
                <td className="p-3 font-semibold">{item.title}</td>
                <td className="p-3">
                  {item.type === "video" ? "üé• ŸÅŸäÿØŸäŸà" : "üì∞ ŸÖŸÇÿßŸÑ"}
                </td>
                <td className="p-3">
                  <span
                    className={`px-2 py-1 rounded text-sm ${
                      item.status === "published"
                        ? "bg-green-600"
                        : item.status === "draft"
                        ? "bg-yellow-600"
                        : "bg-gray-600"
                    }`}
                  >
                    {item.status}
                  </span>
                </td>
                <td className="p-3">
                  {item.is_breaking && (
                    <span className="bg-red-600 px-2 py-1 rounded text-xs">
                      ÿπÿßÿ¨ŸÑ
                    </span>
                  )}
                </td>
                <td className="p-3">
                  {item.published_at
                    ? new Date(item.published_at).toLocaleDateString()
                    : "‚Äî"}
                </td>
                <td className="p-3 flex gap-2">
                  <button
                    onClick={() =>
                      router.push(`/admin/news/${item.uuid}`)
                    }
                    className="bg-blue-600 px-3 py-1 rounded hover:bg-blue-700"
                  >
                    ÿ™ÿπÿØŸäŸÑ
                  </button>
                  <button
                    onClick={() => handleDelete(item.uuid)}
                    className="bg-red-600 px-3 py-1 rounded hover:bg-red-700"
                  >
                    ÿ≠ÿ∞ŸÅ
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        {filtered.length === 0 && (
          <p className="text-center text-gray-500 py-6">
            ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿ£ÿÆÿ®ÿßÿ±
          </p>
        )}
      </div>
    </div>
    </AdminLayout>
  );
}
